import request from '../../util/request.js'
import parser from "@/common/jyf-parser/components/jyf-parser/jyf-parser"



export default {
	components: {
	    "jyf-parser": parser
	  },
	data() {
		return {
			detail: null,
			html:'',
		}
	},
	onLoad: function(options) {
		const {
			id
		} = options;
		console.log(id);
		request.post('/findPostDetail', {
			id,
			page: 1
		}).then(res => {
			console.log(res);
			this.detail = res.p
			this.html = res.p.content
		})
	},
	methods: {

	}
}
